
.search-rettangolo{
  border-width: 0;
  position:relative;
  height: 3.125em; //50px;
  background: inherit;
  background-color: rgba(255, 255, 255, 1);
  box-sizing: border-box;
  border-width: 0.0625em; //1px;
  border-style: solid;
  border-color: rgba(37, 137, 0, 1);
  border-radius: 2.5em; //40px;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  z-index: 1;

}
.input-container{
  //width: 49.38em; //790px;
}
.input-text-search{
  margin-left:  12px; //26px;
  margin-top:  1px; //2px;
  //width:  43.75em; //700px;
  width: 90%;
  height:  2.88em; //46px;
  font-family: 'Arial';
  font-weight: 400;
  font-style: normal;
  font-size: 1em; //16px;
  text-decoration: none;
  color: #323232;
  text-align: left;
  border: 0;
  outline-style: none;
}

.input-text-search::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}
.span-close{
  float: right;
  vertical-align: middle;
  line-height: 1.77em;
  //line-height: 2.81em; //45px;
  padding-right: 0.75em; //12px;
  font-size: 1.63em; //26px;
  color: rgba(37, 137, 0, 1);
}
.search-icon{
  font-size: 1.63em; //26px;
  color: rgba(37, 137, 0, 1);
}
.title-cont{
  border-width: 0;
  position: absolute;
  left:  1.25em; //20px;
  padding-top:  0.625em; //10px;
  //width:  44.63em; //714px;
  word-wrap: break-word
}
.btn-margin{
  margin-left: 6.25em; //100px;
}

.container-result{
  margin-top: 3.125em;
  border-width: 0;
  position: absolute;
  left: 1.6em; //28px;
  //width: 46em; //736px;
  width: 93%;
  padding-top:  0.625em; //10px;
  // margin-top: 22px;
  min-height:  6.25em; //100px;
  background: inherit;
  background-color: rgba(255, 255, 255, 1);
  box-sizing: border-box;
  border-width: 0.06em; //1px;
  border-style: solid;
  border-color: rgba(37, 137, 0, 1);
  border-top: 0;
  border-radius: 0;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  font-size:  1em; //16px (prima era 12px);
  color: #323232;
  z-index: 10000;
}
.title-results{
  font-size:  0.75em; //12px;
  color: #323232;
  height: 0.875em; //14px;


}
.bottom-result{
  font-family: 'Arial Bold', 'Arial';
  font-weight: 700;
  font-style: normal;
  font-size: 0.688em; //11px;
  color: #258900;
  text-align: center;
  bottom: 0.625em; //10px;
  margin-bottom: 1.4375em;
  p {
    margin-top: 0.562em; //20px;
  }
}



.group{
  padding: 0 1.250em 0.875em 1.250em; //0 20px 0 20px;
  .group-title{
    padding: 0.313em 0.938em 0.562em 0.938em; //5px 15px 5px 15px;
    vertical-align: middle;
    text-align: left;
    line-height: 0.938em; //15px;
    font-family: 'Arial Bold', 'Arial';
    font-weight: 700;
    font-style: normal;
    font-size: 0.750em; //12px;
    color: #323232;
  }
  .row-line{
    height: 2.625em; //42px;
    background-color: rgba(248, 248, 248, 1);
    cursor: pointer;

  }
  table{
    width: 100%;
  }
  tr{
    border-top: 0.063em solid rgba(204, 204, 204, 1);
    //1px solid rgba(204, 204, 204, 1);
  }
  tr:last-child {
    border-bottom: 0.063em solid rgba(204, 204, 204, 1);
    //1px solid rgba(204, 204, 204, 1);
  }
  .info{
    float: left;
    width: 80%;
    padding-left: 0.938em; //15px;
    padding-top: 0.313em; //5px;
    text-align: left;
  }
  .info-label-data{
    font-family: 'Arial Bold', 'Arial';
    font-weight: 700;
    font-style: normal;
    font-size: 0.750em; //12px;
    color: #323232;
  }
  .info-label-desc{
    font-family: 'Arial Bold', 'Arial';
    font-weight: 400;
    font-style: normal;
    font-size: 0.688em; //11px;
    color: #323232;
  }
  .label-acc{
    float: right;
    width: 20%;
    text-align: right;
    line-height: 2.625em; //42px;
    vertical-align: middle;
    padding-right: 0.625em; //10px;
    font-family: 'Arial Bold', 'Arial';
    font-weight: 700;
    font-style: normal;
    font-size: 0.750em; //12px;
    color: #323232;
  }
}
